import React from "react";

import errorSVG from "./error.svg";
import "./ErrorIndicator.css"

const ErrorIndicator = ({ statusError, textError }) => {
    return (
        <div className="error-indicator">
			<h2 className="error-indicator__status">{ statusError }</h2>
			<img className="error-indicator__img" src={errorSVG} alt="error" />
			<h2 className="error-indicator__text">{ textError }</h2>
		</div>
    );
};

ErrorIndicator.defaultProps = {
    statusError: null,
    textError: " - ..."
};

export default ErrorIndicator;