/*
*	     
*/
export const getSize = (elem) => {
	let box = elem.getBoundingClientRect();

	return {
		width: box.right - box.left,
		height: box.bottom - box.top
	}
}

/*
*	      
*/
export const getAmountLine = (widthDocument, setting) => {
	return Math.floor((widthDocument - (widthDocument * setting.gap / setting.width)) / setting.width);
}

/*
**    
*/
export const createPositions = (elements, amountLine, gap, stopIter, stopWidth, stopHeights) => {

    let sizes = {},
        length				= elements.length,
		positions			= [],
		width				= [],
		height				= (!!stopHeights && !!stopHeights.length ) ? stopHeights : [],
		maxWidth			= 0,
		maxHeight			= 0,
		currentWidth		= 0,
		currentHeight		= 0,
		prevHeight			= 0,
		newWidth			= stopWidth || 0,
		newHeight			= 0,
		x					= stopIter || 0;

	for(let i = 0; i < length; i++, x++){

		if(x === amountLine){
			x = 0;
			newWidth = 0;
		}

		currentWidth = getSize(elements[i].current).width;
		currentHeight = getSize(elements[i].current).height;
		prevHeight	= height[x] || 0;

		positions[i] = {
			width: currentWidth,
			height: currentHeight,
			top: prevHeight,
			left: newWidth
		};
	
		newWidth = newWidth + currentWidth + gap;
		newHeight = prevHeight + currentHeight + gap;

		width[i] = newWidth;

		height[x] = newHeight;
	}

	maxWidth	= Math.max.apply(null, width);
	maxHeight	= Math.max.apply(null, height);

	maxWidth	= maxWidth.toFixed(0);
	maxHeight	= maxHeight.toFixed(0);

	sizes = {
		width: maxWidth - gap,
		height: maxHeight - gap,
		positions: positions,
		stopIter: x,
		stopWidth: newWidth,
		stopHeights: height
	};
	
	return sizes;

}