const initialState = {
    allPhotos: [],
    singlePhoto: {},
    allAlbums: [],
    loading: {
        all: true,
        single: true,
        allAlbums: true,
        count: 0
    },
    error: {
        text: null,
        status: false
    }
};

const firestoreReducer = (state = initialState, action) => {

    switch(action.type){

        // FETCH REQUEST
        case "FETCH_REQUEST_ALL":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    all: true,
                    count: state.loading.count + 1
                }
            }
        case "FETCH_REQUEST_SINGLE":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    single: true,
                    count: state.loading.count + 1
                }
            }
        case "FETCH_REQUEST_ALL_ALBUMS":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    allAlbums: true,
                    count: state.loading.count + 1
                }
            }

        // FETCH SUCCESS
        case "FETCH_All_PHOTOS_SUCCESS":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    all: false,
                    count: state.loading.count - 1
                },
                allPhotos: action.payload
            }
        case "FETCH_SINGLE_PHOTO_SUCCESS":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    count: state.loading.count - 1,
                    single: false
                },
                singlePhoto: action.payload
            }
        case "FETCH_All_ALBUMS_SUCCESS":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    allAlbums: false,
                    count: state.loading.count - 1
                },
                allAlbums: action.payload
            }

        // FETCH ERROR
        case "FETCH_All_PHOTOS_ERROR":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    count: state.loading.count - 1,
                    all: false
                },
                error: {
                    text: action.payload,
                    status: true
                }
            }
        case "FETCH_SINGLE_PHOTO_ERROR":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    count: state.loading.count - 1,
                    single: false
                },
                error: {
                    text: action.payload,
                    status: true
                }
            }
        case "FETCH_All_ALBUMS_ERROR":
            return {
                ...state,
                loading: {
                    ...state.loading,
                    allAlbums: false,
                    count: state.loading.count - 1
                },
                error: {
                    text: action.payload,
                    status: true
                }
            }

        default:
            return state;
        
    }
};

export default firestoreReducer;