// FETCH REQUEST
const loadingStartAll = () => {
    return {
        type: "FETCH_REQUEST_ALL"
    };
}
const loadingStartSingle = () => {
    return {
        type: "FETCH_REQUEST_SINGLE"
    };
}
const loadingStartAllAlbums = () => {
    return {
        type: "FETCH_REQUEST_ALL_ALBUMS"
    };
}

// FETCH SUCCESS
const photosLoaded = (allPhotos) => {
    return {
        type: "FETCH_All_PHOTOS_SUCCESS",
        payload: allPhotos
    };
}
const photoLoaded = (singlePhoto) => {
    return {
        type: "FETCH_SINGLE_PHOTO_SUCCESS",
        payload: singlePhoto
    };
}
const albumsLoaded = (allAlbums) => {
    return {
        type: "FETCH_All_ALBUMS_SUCCESS",
        payload: allAlbums
    };
}

// FETCH ERROR
const photosError = (error) => {
    return {
        type: "FETCH_All_PHOTOS_ERROR",
        payload: error
    };
}
const photoError = (error) => {
    return {
        type: "FETCH_SINGLE_PHOTO_ERROR",
        payload: error
    };
}
const albumsError = (error) => {
    return {
        type: "FETCH_All_ALBUMS_ERROR",
        payload: error
    };
}




const fetchAllPhotos = (method) => (dispatch) => {
    dispatch(loadingStartAll());
    method()
        .then((querySnapshot) => {

            let data = [];

            if(querySnapshot.empty){
                dispatch(photosError("    ..."));
            }
            else{
                querySnapshot.forEach((doc) => {
                    data.push({ id: doc.id, ...doc.data() });
                });
                dispatch(photosLoaded(data));
            }
            
        })
        .catch(() => {
            dispatch(photosError("    ..."));
        });
};

const fetchSinglePhoto = (id, method) => (dispatch) => {
    dispatch(loadingStartSingle());
    method(id)
        .then((doc) => {
            if (doc.exists) {
                dispatch(photoLoaded(doc.data()));
            } else {
                dispatch(photoError("   ..."))
            }
        }).catch(() => {
            dispatch(photoError("    ..."))
        });
};

const fetchAllAlbums = (method) => (dispatch) => {
    dispatch(loadingStartAllAlbums());
    method()
        .then((querySnapshot) => {

            let data = [];

            if(querySnapshot.empty){
                dispatch(albumsError("    ..."));
            }
            else{
                querySnapshot.forEach((doc) => {
                    data.push({ id: doc.id, ...doc.data() });
                });
                dispatch(albumsLoaded(data));
            }
            
        })
        .catch(() => {
            dispatch(albumsError("    ..."));
        });
};

const fetchSingleAlbum = (album, method) => (dispatch) => {
    dispatch(loadingStartAll());
    method(album)
        .then((querySnapshot) => {

            let data = [];
            
            if(querySnapshot.empty){
                dispatch(photosError("    ..."));
            }
            else{
                querySnapshot.forEach((doc) => {
                    data.push({ id: doc.id, ...doc.data() });
                });
                dispatch(photosLoaded(data));
            }
            
        })
        .catch(() => {
            dispatch(photosError("    ..."));
        });
};

export { fetchAllPhotos, fetchSinglePhoto, fetchAllAlbums, fetchSingleAlbum };