import React from "react";

import { FirestoreServiceConsumer } from "../context";

const withFirestoreService = () => (Wrapper) => {
    return (props) => {
        return (
            <FirestoreServiceConsumer>
                {
                    (firestoreService) => {
                        return <Wrapper {...props} {...firestoreService} />
                    }
                }
            </FirestoreServiceConsumer>
        );
    }
}

export default withFirestoreService;