import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import { withRouter, Route } from "react-router-dom";

import Album from './album';
import MasonryDI from './../../masonry-di';
import MasonryDIViewBox from './../../masonry-di-viewbox';
import ErrorIndicator from "../../error-indicator";

import { fetchAllAlbums } from "../../../store/actions";

import withFirestoreService from "../../../hoc";

import "./Albums.css"



class Albums extends Component {

    componentDidMount(){
        this.props.fetchAllAlbums();
    }

    hasAlbum = () => {
        const { name: albumName } = this.props.match.params;

        return albumName;
    }

    render(){

        const { albums, error, loading, match } = this.props;
        const { text, status } = error;
        const { album } = match.params;
        
        if(loading){
            return null;
        }

        if(status){
            return <ErrorIndicator textError={ text } />
        }


        return (
            <div className="albums">
                <Route path={`${match.url}/photo/:id/size/:props`} render={() => {
                    return <MasonryDIViewBox />
                }} />

                { album && <Album /> }
                {
                    !album && <MasonryDI
                                    data={ albums }
                                    width={ 300 } />
                }
                
            </div>
        );
    }
    
};

const mapStateToProps = (state) => {
    return {
        albums: state.photos.allAlbums,
        loading: state.photos.loading.allAlbums,
        error: state.photos.error
    }
};

const mapDispatchToProps = (dispatch, { getAllAlbums }) => {

    return {
        fetchAllAlbums: () => dispatch(fetchAllAlbums(getAllAlbums))
    }
}

export default compose(
    withRouter,
    withFirestoreService(),
    connect(mapStateToProps, mapDispatchToProps)
)(Albums);