import firebaseConfig from "./config";
import firebase from 'firebase/app';
import 'firebase/auth';
import "firebase/firestore";

export default class FirestoreService {

    constructor(){

        // Initialize Firebase
        firebase.initializeApp(firebaseConfig);
        this.db = firebase.firestore();

    }

    getAllPhotos = async () => {
        return await this.db.collection("photos").get();
    }
    getSinglePhoto = async (id) => {
        return await this.db.collection("photos").doc(id).get();
    }

    getAllAlbums = async () => {
        return await this.db.collection("albums").get();
    }
    getSingleAlbum = async (album) => {
        return await this.db.collection("photos").where("albumHash", "==", `${album}`).get();
    }

};