import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { compose } from "redux";
import { connect } from 'react-redux';
import withFirestoreService from "../../../../hoc";

import MasonryDI from './../../../masonry-di';

import ErrorIndicator from "../../../error-indicator";

import { fetchSingleAlbum } from "../../../../store/actions";


class Album extends Component {

    componentDidMount(){
        this.props.fetchPhotos();
    }

    render(){

        const { photos, loading, error } = this.props;
        const { text, status } = error;

        if(loading){
            return null;
        }

        if(status){
            return <ErrorIndicator textError={ text } />
        }

        return (
            <div className="album" >
                <MasonryDI 
                    data={ photos }
                    width={ 300 } />
            </div>    
        )
    }
    
};

const mapStateToProps = (state) => {
    return {
        photos: state.photos.allPhotos,
        loading: state.photos.loading.all,
        error: state.photos.error
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {

    const { match, getSingleAlbum } = ownProps;
    const { params: { album } } = match;

    return {
        fetchPhotos: () => dispatch(fetchSingleAlbum(album, getSingleAlbum))
    };
};

export default compose(
    withRouter,
    withFirestoreService(),
    connect(mapStateToProps, mapDispatchToProps)
)(Album);