import React, { Component } from "react";
import { connect } from "react-redux";

import anime from "animejs/lib/anime";
import { getTotalLength } from "../../utils";

import "./LoadingLine.css"

class LoadingLine extends Component {

    constructor(){
        super();
        
        this.loadingLine = null;
        this.loadingLineLength = null;
        this.loadingLineThree = null;
        this.loadingAnimation = null;

    }

    componentDidMount(){

        const { countRequest } = this.props;



        if(!!countRequest === true){
            this.startAnimation();
            console.log("componentDidMount", "startAnimation");
        }
    }

    componentDidUpdate(prevProps){

        const { countRequest } = this.props;

        

        if(!!countRequest  === true && prevProps.countRequest !== countRequest){
            this.startAnimation();
            console.log("componentDidUpdate", "startAnimation");
        }

        if(!!countRequest === false){
            this.completeAnimation();
            console.log("componentDidUpdate", "completeAnimation");
        }
    }

    startAnimation = () => {
        const self = this;

        this.loadingLineLength		= getTotalLength(this.loadingLine);
        this.loadingLineThree		= this.loadingLineLength / 6;

        if(this.loadingAnimation !== null){
            this.loadingAnimation.pause(); 
        }

        this.loadingAnimation = anime({
            targets: self.loadingLine,
            easing: "easeInOutSine",
            duration: 1400,
            strokeDasharray: [self.loadingLineLength + " " + self.loadingLineLength, self.loadingLineThree + " " + self.loadingLineLength],
            strokeDashoffset: [self.loadingLineLength, -self.loadingLineLength],
            loop: true
        });
    }

    completeAnimation = () => {
        const self = this;

        // if(this.loadingAnimation !== null){
        //     this.loadingAnimation.pause(); 
        // }

        this.loadingAnimation.pause(); 

        this.loadingAnimation = anime({
            targets: self.loadingLine,
            easing: "easeInOutSine",
            duration: 800,
            strokeDasharray: self.loadingLineLength + " " + self.loadingLineLength,
            strokeDashoffset: 0
        });
    }

    render(){

        return (
            <div className="loading-line">
                <svg version="1.1" id="svg-loading-line" preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px"  
                viewBox="0 0 1920 20" >
                    <linearGradient id="SVGID_1_" gradientUnits="userSpaceOnUse" x1="0" y1="10" x2="1920" y2="10">
                        <stop  offset="0.08" stopColor="#E0CD1E" />
                        <stop  offset="0.92" stopColor="#1FB9DD" />
                    </linearGradient>
                    <rect fill="url(#SVGID_1_)" width="1920" height="20" mask="url('#mask-loading-line')" />
                    <mask id="mask-loading-line">
                        <line id="path-loading-line" ref={ (ref) => this.loadingLine = ref } fill="none" stroke="#FFFFFF" strokeWidth="28.3465" strokeMiterlimit="10" x1="0" y1="10.63" x2="1920" y2="10"/>
                    </mask>
                </svg>
            </div>
        );
    }
    
};

const mapStateToProps = (state) => {
    return {
        countRequest: state.photos.loading.count,
        loadingAll: state.photos.loading.all,
        loadingSingle: state.photos.loading.single,
    }
};

export default connect(mapStateToProps)(LoadingLine);