import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";

import { withRouter } from "react-router-dom";
import withFirestoreService from "../../hoc";

import { fetchSinglePhoto } from "../../store/actions";

import anime from "animejs/lib/anime";
import { getTotalLength } from "../../utils";

import "./MasonryDIViewBox.css"

class MasonryDIViewBox extends Component {

	constructor(){
		super()

		this.state = {
			width: 0,
			height: 0,
			ready: false,
			resize: false
		};

		this.params = null;

		this.widthView		= null;
		this.heightView		= null;
		this.percentView	= null;
		this.factorView		= null;
		this.factorImg		= null;

		this.elements = {
			rectY: null,
			rectB: null
		};
	}

	componentDidMount(){
		this.getSizePhoto();

		window.addEventListener("resize", this.resize);

		this.params = this.props.match.params.props.split(",");
		this.props.fetchPhoto();

	}

	componentDidUpdate(prevProps, prevState){
		if(prevProps.loading !== this.props.loading){
			this.setSize(this.percentView, this.factorView, this.params[2], false);
		}
		if(prevState.ready !== this.state.ready){
			this.runAnimation(this.elements)
		}
	}

	componentWillUnmount(){
		window.removeEventListener("resize", this.resize);
	}

	shouldComponentUpdate(nextProps, nextState){

		if(nextProps.photo !== this.props.photo){
			return true;
		}

		if(nextState.ready !== this.state.ready){
			return true;
		}
		if(nextState.resize && this.state.resize){
			return true;
		}

		return false;

	}
	getSizePhoto = () => {
		this.widthView = document.documentElement.clientWidth;
		this.heightView = document.documentElement.clientHeight;
		this.percentView = this.getPercent(this.widthView, this.heightView, 76);
		this.factorView = this.getFactor(this.widthView, this.heightView);
	}
	resize = () => {
		this.getSizePhoto();
		
		this.setSize(this.percentView, this.factorView, this.params[2], true);
		this.runAnimation(this.elements)
	}

	getFactor = (width, height) => {
		return width / height;
	}

	getPercent = (width, height, percent) => {
		return {
			percentW: width / 100 * percent,
			percentH: height / 100 * percent
		}
	}

	setSize = (percentView, factorView, factorImg, resize) => {

		let widthView, widthViewFactor,
			heightView, heightViewFactor;
	
		widthView			= percentView.percentW;
		heightView			= percentView.percentH;
		widthViewFactor		= percentView.percentW / factorImg;
		heightViewFactor	= percentView.percentH * factorImg;
		
		if(factorView > factorImg){
			
			this.setState({
				width: heightViewFactor,
				height: heightView,
				ready: true,
				resize
			});
		}

		if(factorView < factorImg){
			
			this.setState({
				width: widthView,
				height: widthViewFactor,
				ready: true,
				resize
			});
		}
		
		if(factorView === factorImg){
			
			this.setState({
				width: widthView,
				height: widthView,
				ready: true,
				resize
			});
		}
	}

	runAnimation = (elements) => {
		let pathLength, path1, path2, half, third, dasharray;

		path1 = elements.rectY;
		path2 = elements.rectB;
		
		pathLength = getTotalLength(path1);
		
		half = pathLength / 2;
		third = pathLength / 3;
		dasharray = pathLength - third;

		anime({
			targets: [path1, path2],
			easing: "easeInOutSine",
			duration: 1000,
			strokeDasharray: function(i, x){
				return x === 0 ? [pathLength, third + " " + dasharray] : [pathLength, third + " " + dasharray]
			},
			strokeDashoffset: function(i, x){
				return x === 0 ? 0 : half
			},
		})

		anime({
			targets: [path1, path2],
			easing: "linear",
			dealy: 1200,
			duration: 16000,
			strokeDashoffset: function(i, x){
				return x === 0 ? [0, -pathLength] : [half, -pathLength + half]
			},
			loop: true
		});
	}

	render(){

		const { history, photo: { src } } = this.props;
		const { width, height, ready } = this.state;

		const style = {
			width,
			height
		}

		return (
			
			ready && <div className="masonry-di-viewbox">
				<div className="masonry-di-viewbox__close" onClick={ () => history.action === "POP" ? history.push("/") : history.goBack() } ></div>
				<div className="masonry-di-viewbox__wrap" style={ style } >
					<div className="masonry-di-viewbox__sqr masonry-di-viewbox__sqr--yellow">
						<svg version="1.1" id="sqrYellow" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" viewBox={`0 0 ${width} ${height}`} x="0px" y="0px" >
							<rect fill="none" ref={ (ref) => this.elements.rectY = ref } stroke="#e0cd1e" x="0" y="0" width="100%" height="100%"  />
						</svg>
					</div>
					<div className="masonry-di-viewbox__sqr masonry-di-viewbox__sqr--blue">
						<svg version="1.1" id="sqrBue"  xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" viewBox={`0 0 ${width} ${height}`} x="0px" y="0px"   >
							<rect fill="none" ref={ (ref) => this.elements.rectB = ref } stroke="#1fb9dd" x="0" y="0" width="100%" height="100%" />
						</svg>
					</div>
					<figure className="masonry-di-viewbox__figure">
						<img src={ src } alt="" className="masonry-di-viewbox__image" />
					</figure>
				</div>
			</div>
		);
	}
    
}

const mapStateToProps = (state) => {
	return {
		photo: state.photos.singlePhoto,
		loading: state.photos.loading.single
	}
};

const mapDispatchToProps = (dispatch, { match, getSinglePhoto }) => {
	const { id } = match.params;

	return {
		fetchPhoto: () => dispatch(fetchSinglePhoto(id, getSinglePhoto))
	}
};

export default compose(
	withRouter,
	withFirestoreService(),
	connect(mapStateToProps, mapDispatchToProps)
)(MasonryDIViewBox);