import React, { Component, Fragment } from "react";
import { compose } from "redux";
import { connect } from "react-redux";

import { Route, withRouter } from "react-router-dom";

import ErrorIndicator from "../../error-indicator";

import MasonryDIViewBox from './../../masonry-di-viewbox';
import MasonryDI from './../../masonry-di';

import withFirestoreService from "../../../hoc";

import { fetchAllPhotos } from "../../../store/actions";


class Home extends Component {

    componentDidMount(){
        this.props.fetchPhotos();
    }

    render(){

        const { photos, error, loading, match } = this.props;
        const { text, status } = error;
        
        if(loading){
            return null;
        }

        if(status){
            return <ErrorIndicator textError={ text } />
        }

        return (
            <Fragment>
                <Route path="/photo/:id/size/:props" component={ MasonryDIViewBox } />
                <MasonryDI
                    data={ photos }
                    width={ 300 } />
            </Fragment>
        );
    }
    
};

const mapStateToProps = (state) => {
    return {
        photos: state.photos.allPhotos,
        loading: state.photos.loading.all,
        error: state.photos.error
    }
};

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        fetchPhotos: () => dispatch(fetchAllPhotos(ownProps.getAllPhotos))
    };
};

export default compose(
    withRouter,
    withFirestoreService(),
    connect(mapStateToProps, mapDispatchToProps),
)(Home);