import React, { Component } from "react";

import { createPositions, getAmountLine } from "./lib"
import MasonryDIItem from "./masonry-di-item";

import "./MasonryDI.css"

class MasonryDI extends Component {
    constructor(props){
        super(props);

        this.factor                     = [];
        this.elementsRef                = [];
        this.containerRef               = null;
        this.widthDocument          	= 0;
        this.amountLine                 = 0;
        this.oldAmountLine              = 0;
        this.defaultWidth               = this.props.width;
        this.setting = {
            width: this.props.width,
            gap: this.props.gap,
            responsive: this.props.responsive
        };

        this.state = {
            positions: null,
            widthContainer: 0,
            heightContainer: 0,
            resize: false,
            stopIter: null,
            stopWidth: null,
            stopHeights: null
        };
    }

    componentDidMount(){
        this.loadMasonryDI();
        window.addEventListener("resize", this.resizeMasonryDI);
    }

    componentDidUpdate(prevProps){

        if(!!prevProps.data && prevProps.data !== this.props.data){

            const currentRef = this.elementsRef.slice(prevProps.data.length);
            const sizes = createPositions(currentRef, this.amountLine, this.setting.gap, this.state.stopIter, this.state.stopWidth, this.state.stopHeights);

            this.setState( ( { positions } ) => {
                return {
                    positions: [...positions, ...sizes.positions],
                    heightContainer: sizes.height
                };
            });
        }

        if(this.state.resize){

            const sizes = createPositions(this.elementsRef, this.amountLine, this.setting.gap);

            this.setState({
                positions: sizes.positions,
                widthContainer: sizes.width,
                heightContainer: sizes.height,
                resize: false,
                stopIter: sizes.stopIter,
                stopWidth: sizes.stopWidth,
                stopHeights: sizes.stopHeights
            });

       }
    }

    componentWillUnmount(){
        window.removeEventListener("resize", this.resizeMasonryDI);
    }
    
    loadMasonryDI(){
        this.widthDocument = document.documentElement.clientWidth - this.setting.gap;

        this.setResponse(this.widthDocument, this.setting.responsive);
        this.amountLine = getAmountLine(this.widthDocument, this.setting);
        this.amountLine = this.amountLine < 1 ? 1 : this.amountLine;

        this.oldWidthItem = this.setting.width;
        this.oldAmountLine = this.amountLine; 

        this.setState({
            resize: true
        });

    }

    resizeMasonryDI = (e) => {

        this.widthDocument = document.documentElement.clientWidth - this.setting.gap;

        this.setResponse(this.widthDocument, this.setting.responsive);

        this.amountLine = getAmountLine(this.widthDocument, this.setting);
        this.amountLine = this.amountLine < 1 ? 1 : this.amountLine;

        if(this.oldAmountLine !== this.amountLine || this.oldWidthItem !== this.setting.width){

            this.oldWidthItem = this.setting.width;
            this.oldAmountLine = this.amountLine; 

            this.setState({
                resize: true
            });
        }        

    }

    setResponse(w, breakpoints){
		
		let i, status;
        this.setting.width = this.defaultWidth;

        if(!!breakpoints && !!breakpoints.length){
            i = breakpoints.length;
            while(i--){
                status = w < breakpoints[i].media;
                if(status){
                    this.setting.width = breakpoints[i].widthItem;
                    break;
                }
            }
		}
    }

    render(){

        const { data } = this.props;
        const { widthContainer, heightContainer } = this.state;

        if(!data){
            return null;
        }

        const styleContainer = {
            width: widthContainer,
            height: heightContainer
        };

        const items = data.map((item, i) => {

            const ref = React.createRef();
            this.elementsRef[i] = ref;

            this.factor[i] = item.width / item.height;

            let top = null,
                left = null;

            if(!!this.state.positions && !!this.state.positions[i]){
                top = this.state.positions[i].top;
                left = this.state.positions[i].left;
            }

            return (
                <MasonryDIItem
                        key={i}
                        item={ item }
                        itemRef={ ref }
                        width={ this.setting.width }
                        factor={ this.factor[i] }
                        top={ top }
                        left={ left }
                        resize={ this.state.resize }
                        shouldUpdate={ top !== null && left !== null } />
            )
            
        });

        return (
            <div className="masonry-di" style={ styleContainer } ref={ (ref) => this.containerRef = ref }>
                { items }
            </div>
        );
    }
};

MasonryDI.defaultProps = {
    width: 200,
    gap: 15
};

export default MasonryDI;